import React from "react";

import { withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";

import Logo from "./../logo.png";

import myTheme from "../util/myTheme";

const styles = theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: myTheme.primary.orange
  },
  logo: {
    marginLeft: theme.spacing(2)
  },
  buttons: {
    marginRight: theme.spacing(2),
    color: myTheme.text.indigo.dark
  },
  title: {
    flexGrow: 1
  }
});

class NavBar extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <AppBar position="static" className={classes.root}>
        <Toolbar>
          <IconButton edge="start" className={classes.logo} aria-label="menu">
            <img src={Logo} width={75} />
          </IconButton>
          <Typography variant="h6" className={classes.title}></Typography>
          <Button className={classes.buttons}>Contact</Button>
        </Toolbar>
      </AppBar>
    );
  }
}

export default withStyles(styles)(NavBar);
