import React, { Component } from "react";

import { withStyles } from "@material-ui/styles";

import myTheme from "../util/myTheme";

const styles = theme => ({
  about: {
    backgroundColor: myTheme.primary.orange,
    paddingBottom: theme.spacing(25),
  },
  title: {
    color: myTheme.text.indigo.dark,
    textAlign: "center",
    fontFamily: "Lobster Two",
    marginTop: 0,
    paddingTop: 25,
    [theme.breakpoints.only("xs")]: {
      fontSize: 50
    },
    [theme.breakpoints.only("sm")]: {
      fontSize: 55
    },
    [theme.breakpoints.only("md")]: {
      fontSize: 60
    },
    [theme.breakpoints.only("lg")]: {
      fontSize: 65
    },
    [theme.breakpoints.only("xl")]: {
      fontSize: 72
    }
  },
  text: {
    textAlign: "center",
    fontFamily: "Comfortaa",
    marginRight: theme.spacing(10),
    marginLeft: theme.spacing(10),
    color: myTheme.text.lightBlue.dark,
    [theme.breakpoints.only("xs")]: {
      fontSize: 17.5
    },
    [theme.breakpoints.only("sm")]: {
      fontSize: 20
    },
    [theme.breakpoints.only("md")]: {
      fontSize: 20
    },
    [theme.breakpoints.only("lg")]: {
      fontSize: 25
    },
    [theme.breakpoints.only("xl")]: {
      fontSize: 25
    }
  },
  brMobile: {
    [theme.breakpoints.up("sm")]: {
      display: "none"
    }
  }
});
class About extends Component {
  render() {
    const { classes } = this.props;

    return (
      <div className={classes.about}>
        <p className={classes.title}>
          HI, I'm Octavian.<br className={classes.brMobile}></br> Nice to meet
          you!
        </p>
        <p className={classes.text}>
          I'm a student in 9th grade at High School Computer Science "Grigore
          Moisil", Iași, Romania.
          <br />
          I'm very passionate about programming, this is like a daily habit.
          <br />
          Being such a curious person, I really enjoy learning new things and
          challenges are not a problem for me.
        </p>
      </div>
    );
  }
}

export default withStyles(styles)(About);
