import React, { Component } from "react";

import { withStyles } from "@material-ui/styles";
import Grid from "@material-ui/core/Grid";

import Unity from "../images/unity.png";

import myTheme from "../util/myTheme";

const styles = theme => ({
  CV: {
    marginTop: theme.spacing(-10),
    backgroundColor: "white",
    border: 1,
    borderRadius: 25,
    borderColor: "white",
    borderStyle: "solid",
    marginLeft: theme.spacing(15),
    marginRight: theme.spacing(15),
    boxShadow: "0px 0px 10px gray",
    textAlign: "center",
    fontFamily:"Comfortaa"
  },
  container: {},
  cell: {
    borderRight: 3,
    borderRightColor: myTheme.colors.blue.primary,
    borderRightStyle: "solid",
    "&:last-child": {
      borderRight: 0
    }
  },
  image: {
    width: 50,
    marginLeft: "auto",
    marginRight: "auto",
    display: "block",
    paddingTop: theme.spacing(5)
  },
  title: {
    fontSize: 30,
    fontFamily:"Bai Jamjuree !important"
  },
  description: {
    fontSize: 20,
    marginLeft:theme.spacing(1),
    marginRight:theme.spacing(1)
  },
  subtitle: {},
  element: {}
});

class CV extends Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.CV}>
        <Grid container className={classes.container}>
          <Grid item xs={12} md={4} className={classes.cell}>
            <div>
              <p className={classes.title}>Front-end Developer</p>
              <p className={classes.description}>
                varius morbi enim nunc faucibus a pellentesque sit amet
                porttitor eget dolor morbi non arcu risus quis varius quam
                quisque id diam vel quam elementum
              </p>
              <p className={classes.subtitle}>aaaaaaaaaaaaaaa</p>
              <p className={classes.element}>
                viverra
                <br />
                diam
                <br />
                quisque
                <br />
                porttitor
                <br />
                morbi
                <br />
                pellentesque
                <br />
              </p>
            </div>
          </Grid>
          <Grid item xs={12} md={4} className={classes.cell}>
            <div>
              <p className={classes.title}>Back-end Developer</p>
              <p className={classes.description}>
                varius morbi enim nunc faucibus a pellentesque sit amet
                porttitor eget dolor morbi non arcu risus quis varius quam
                quisque id diam vel quam elementum
              </p>
              <p className={classes.subtitle}>bbbbbbbb</p>
              <p className={classes.element}>
                viverra
                <br />
                diam
                <br />
                quisque
                <br />
                porttitor
                <br />
                morbi
                <br />
                pellentesque
                <br />
              </p>
            </div>
          </Grid>
          <Grid item xs={12} md={4} className={classes.cell}>
            <div>
              {/* <img alt="Unity" src={Unity} className={classes.image} /> */}
              <p className={classes.title}>Unity</p>
              <p className={classes.description}>
                varius morbi enim nunc faucibus a pellentesque sit amet
                porttitor eget dolor morbi non arcu risus quis varius quam
                quisque id diam vel quam elementum
              </p>
              <p className={classes.subtitle}>cccccccccccccccccc</p>
              <p className={classes.element}>
                viverra
                <br />
                diam
                <br />
                quisque
                <br />
                porttitor
                <br />
                morbi
                <br />
                pellentesque
                <br />
              </p>
            </div>
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default withStyles(styles)(CV);
