import React, { Component } from "react";

import { withStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";

import ProfilePic from "../images/profilepic.png";
import Image1 from "../images/image1.svg";

import myTheme from "../util/myTheme";

const styles = theme => ({
  name: {
    fontFamily: "Lobster Two",
    [theme.breakpoints.only("xs")]: {
      marginTop: theme.spacing(),
      fontSize: 40
    },
    [theme.breakpoints.only("sm")]: {
      marginTop: theme.spacing(),
      fontSize: 50
    },
    [theme.breakpoints.only("md")]: {
      marginTop: theme.spacing(),
      fontSize: 60
    },
    [theme.breakpoints.only("lg")]: {
      marginTop: theme.spacing(),
      fontSize: 65
    },
    [theme.breakpoints.only("xl")]: {
      marginTop: theme.spacing(),
      fontSize: 72
    }
  },
  title: {
    marginTop: theme.spacing(0),
    fontFamily: "Comfortaa",
    fontSize: 50,
    color: myTheme.text.blue.main,
    textAlign: "center",
    [theme.breakpoints.only("xs")]: {
      fontSize: 30
    },
    [theme.breakpoints.only("sm")]: {
      fontSize: 30
    },
    [theme.breakpoints.only("md")]: {
      fontSize: 35
    },
    [theme.breakpoints.only("lg")]: {
      fontSize: 40
    },
    [theme.breakpoints.only("xl")]: {}
  },
  avatar: {
    marginTop: theme.spacing(6),
    marginBottom: theme.spacing(6),
    [theme.breakpoints.only("xs")]: {
      width: 150,
      height: 150
    },
    [theme.breakpoints.only("sm")]: {
      width: 150,
      height: 150
    },
    [theme.breakpoints.only("md")]: {
      width: 150,
      height: 150
    },
    [theme.breakpoints.only("lg")]: {
      width: 150,
      height: 150
    },
    [theme.breakpoints.only("xl")]: {
      width: 200,
      height: 200
    },
    display: "block",
    marginLeft: "auto",
    marginRight: "auto"
  },
  image1: {
    marginTop: theme.spacing(20),
    maxWidth: "100%",
    display: "block",
    marginLeft: "auto",
    marginRight: "auto"
  }
});

export class Intro extends Component {
  render() {
    const { classes } = this.props;
    return (
      <>
        <Avatar
          alt="Profile Picture"
          src={ProfilePic}
          className={classes.avatar}
        />
        <p align="center" className={classes.name}>
          <b>
            <u>Octavian Regatun</u>
          </b>
        </p>
        <p align="center" className={classes.title}>
          Enthusiast programmer,
          <br /> Full Stack and Unity developer
        </p>
        <img src={Image1} className={classes.image1} />
      </>
    );
  }
}

export default withStyles(styles)(Intro);
